	module.exports = {"data":{"post":{"html":"<p><a href=\"http://andrea.editthispage.com/2001/07/30\">Do not</a> trust a physicist with your hair dryer, or any other electrical appliances. Some things are best left to engineers.</p>\n<p>\"Bildungsurlaub\" is paid time off for continuing education. I asked my boss if I could have Bildungsurlaub to learn <a href=\"http://www.vim.org\">vi</a>, but he turned me down.</p>\n<p>I'll bet the Mariners wish they could play the Twins every night.</p>\n<p><a href=\"http://www.fcstpauli.de\">FC St. Pauli</a> opened their Bundesliga season with a 0-0 tie to Hertha BSC Berlin. They'll probably need 40 points to keep from being demoted, and with a budget of just DM 11 million, that's the best anyone can hope for. One down, 39 to go.</p>\n<p>Mama's out of town all week, so Christopher and I will be fending for ourselves until Friday evening.</p>","fields":{"title":"Short Circuit","date":"31 Jul 2001","url":"https://papascott.de/2001-07-31-short-circuit/"}}},"pathContext":{"slug":"/2001-07-31-short-circuit/","prev":{"fields":{"title":"Mama State","date":"30 Jul 2001","slug":"/2001-07-30-mama-state/"}},"next":{"fields":{"title":"Video Killed the Radio Star","date":"01 Aug 2001","slug":"/2001-08-01-video-killed-the-radio-star/"}}}}

